import React, { useState, useEffect } from "react"

import Layout from "../components/layout"
import Seo from "../components/seo"
import { Link } from "gatsby"
import { Row, Col } from "react-bootstrap"
import video1 from "../videos/video_sin_cita.mp4"
import video2 from "../videos/video_cita.mp4"
import video3 from "../videos/video_recoger_carro.mp4"
import portada1 from "../videos/portada1.jpg"
import portada2 from "../videos/portada2.jpg"
import portada3 from "../videos/portada3.jpg"
import icon1 from "../images/iconos/btn_cliente_nuevo.svg"
import icon2 from "../images/iconos/btn_calendario.svg"
import icon3 from "../images/iconos/btn_pagar.svg"
import icon4 from "../images/iconos/tarjetas.svg"

import api from "../components/actions/riveroapi"
import { ThreeCircles } from "react-loader-spinner";

//import * as styles from "../components/pages-css/index.module.css"

export default function IndexPage() {
  const [loading, setLoading] = useState(false)

  //const windowGlobal = typeof window !== 'undefined' && window;
  //console.log("Tengo el token en el localstorage: ", windowGlobal.localStorage.getItem("token"));

  return (
    <Layout backUrl={null}>
      <Seo title="Auto servicio" />
      <div className={loading == false ? `loading` : `loading active`}>
        <ThreeCircles
          height="50%"
          width="50%"
          color="white"
          ariaLabel="three-circles-rotating"
          className="iconLoading"
        />
      </div>
      <Row className="btnAreaHome">
        <Col lg={4}>
          <center>
            <Link to={"/sin-cita"} onClick={() => setLoading(true)}>
              <div className="areaVideo">
                <CrearVideo
                  icono={icon1}
                  titulo={"Sin Cita"}
                  btnName={"Registrarme"}
                  index={1}
                />
                <video poster={portada1} className="bgvid" autoPlay muted loop>
                  <source src={video1} type="video/mp4" />
                </video>
              </div>
            </Link>
          </center>
        </Col>
        <Col lg={4}>
          <center>
            <Link to={"/orden-reparacion"} onClick={() => setLoading(true)}>
              <div className="areaVideo2">
                <CrearVideo
                  icono={icon2}
                  titulo={"Tengo una cita"}
                  btnName={"Confirmar"}
                  index={2}
                />
                <video poster={portada2} className="bgvid" autoPlay muted loop>
                  <source src={video2} type="video/mp4" />
                </video>
              </div>
            </Link>
          </center>
        </Col>
        <Col lg={4}>
          <center>
            <Link to={"/realiza-tu-pago"} onClick={() => setLoading(true)}>
              <div className="areaVideo3">
                <CrearVideo
                  icono={icon3}
                  titulo={"Recoger mi carro"}
                  btnName={"Pagar ahora"}
                  index={3}
                />
                <video poster={portada3} className="bgvid" autoPlay muted loop>
                  <source src={video3} type="video/mp4" />
                </video>
                <center className="tarjetas">
                  <img src={icon4} alt="tarjetas" width={"50%"} />
                </center>
              </div>
            </Link>
          </center>
        </Col>
      </Row>
    </Layout>
  )
}

function CrearVideo({ icono, titulo, btnName, index }) {
  return (
    <div className="areaBotonHome p-4" key={icono}>
      <img src={icono} alt="icono" width={"25%"} />
      <h3 className="text-white">{titulo}</h3>
      <div className={`btn btn-primary btn-lg m-4 homeBtn`}>{btnName}</div>
    </div>
  )
}
